body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 200px;
}

.square {
    background: hsla(10, 0%, 100%, 1);
    border: 1px solid #999;
    font-size: 34px;
    font-weight: bold;
    line-height: 65px;
    height: 65px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 65px;
}

.square--win {
    color: hsla(0, 100%, 50%, 1);
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 100vh;
}

.game-info {
    margin-left: 20px;
}

.history-item,
.history-item:active,
.history-item:focus{
    background-color: transparent;
    border: 0;
    outline-width: 0;
}

.history-item:hover {
    text-decoration: underline;
}

.history-item:active {
    text-decoration: underline;
    color: blue;
}




